import returns from '../../common/returns.vue'
export default {
	data() {
		return {
			title: '评论成功',
			id: '',
			list: [],
			type:'', //判断商品类型 1 是正常的商品订单  2是积分订单
		}
	},
	methods: {
		jump(url) {
			uni.reLaunch({
				url: url
			});
		},
		toDetail(goods_id){
				uni.navigateTo({
					url:'./s_comment_list'+'?goods_id='+goods_id
				})
			
		}
	},
	components: {
		returns
	},
	onLoad(options) {
		this.id = options.id  //订单id
		this.type = options.type
			// type =1 商品评论   为2是积分订单评论
		this.service.entire(this, 'post', this.APIconfig.api_root.subuser.threeuser.s_order_detail, {
			id: options.id,
			user_id: this.$store.state.user.id,
			
			
		}, function(self, res) {
			console.log(res.data.items);
			self.list = res.data.items
		})
	}
}
